.footer-modal-echipa {
  width: 100%;
  height: 100%;
  z-index: 1010;
  background: rgba(0, 0, 0, .7);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content-footer-echipa {
  width: 768px;
  z-index: 1011;
  background: #fff;
  border-radius: 15px;
  flex-direction: column;
  gap: 20px;
  padding: 30px 15px 15px;
  transition: all .3s ease-in-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.close-footer {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  transition: background-color .4s linear, transform .4s ease-in-out;
  display: flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-footer:hover, .close-footer:focus {
  transform: scale(1.5)rotate(90deg);
}

.close-footer > svg {
  stroke: #050505;
  width: 24px;
  height: 24px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-modal-echipa-header {
  color: #050505;
  text-transform: uppercase;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
}

.footer-modal-echipa-text {
  color: #050505;
  max-width: 768px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.modal-footer-name {
  color: #050505;
  text-transform: uppercase;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.modal-footer-role {
  color: #050505;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

.footer-modal-echipa-list {
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 7px;
  display: flex;
}

.footer-modal-echipa-container {
  width: 170px;
  background-color: #9bb537;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.modal-footer-picture {
  width: 120px;
  height: 120px;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid #161616;
}

.svg-footer-echipa {
  fill: #050505;
}

